<script>
  import {
    nickname,
    key,
    clickformopen, refresh_access_token,
  } from "../store.js";

  import { onMount } from "svelte";

  let eventData = [];
  let loading = true;
  let error = null;
  let showDetails = [];

  async function sendRequest(endpoint, method, data) {
  let checkjwt = localStorage.getItem("d2rpvpjwtToken");

  if (!checkjwt) {
    alert(" ");
    return null;
  }

  const sendFetchRequest = async () => {
    const response = await fetch(endpoint, {
      method: method,
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${checkjwt}`,
      },
      body: JSON.stringify(data),
    });
    return response;
  };

  let response = await sendFetchRequest();

  if (response.status === 403) {
    await refresh_access_token();
    checkjwt = localStorage.getItem("d2rpvpjwtToken");
    response = await sendFetchRequest(); //     
  }

  return response;
}

async function deleteandresetEvent(index) {
  try {
    const response = await sendRequest("/cancel-accepted", "DELETE", eventData[index]);

    if (response && response.ok) {
      alert("    ");
    } else {
      alert(` : ${response.status}`);
    }
  } catch (error) {
    alert("  :", error);
  } finally {
    fetchData();
  }
}

async function approveEvent(index) {
  try {
    const response = await sendRequest("/accept-event", "POST", eventData[index]);

    if (response && response.ok) {
      alert("  ");
    } else {
      alert(` : ${response.status}`);
    }
  } catch (error) {
    alert("  :", error);
  } finally {
    fetchData();
  }
}

async function deleteEvent(index) {
  try {
    const response = await sendRequest("/delete-event", "DELETE", eventData[index]);

    if (response && response.status === 200) {
      alert("  ");
    } else {
      alert(` : ${response.status}`);
    }
  } catch (error) {
    alert("  :", error);
  } finally {
    fetchData();
  }
}
  async function fetchData() {
    try {
      const response = await fetch("/eventhistory");
      if (!response.ok) {
        throw new Error(" ");
      }
      eventData = await response.json();

      // eventData 
      eventData = eventData.map((event) => ({
        ...event,
        ok:
          event.accept === 1 ? "" : event.accept === 2 ? "" : event.ok, // event.ok  
      }));

      showDetails = new Array(eventData.length).fill(false); // showDetails  
    } catch (err) {
      error = err;
    } finally {
      loading = false;
    }
  }

  //     
  onMount(async () => {
    const unsubscribe = key.subscribe((value) => {
      fetchData();
    });

    //     
    return () => {
      unsubscribe();
    };
  });

  //   details    
  showDetails = eventData.map(() => false);

  //   
  function toggleDetails(index) {
    showDetails = showDetails.map((show, i) => (i === index ? !show : show));
  }
  function handleKeyDown(event, index) {
    if (event.key === "Enter" || event.key === " ") {
      toggleDetails(index);
    }
  }
</script>

<div class="table-outline main_data">
  <div class="table-head">
    <div class="table-head_eventname table-contents_cell"></div>
    <div class="table-head_member table-contents_cell"></div>
    <div class="table-head_allwinner table-contents_cell"></div>
    <div class="table-head_ok table-contents_right"></div>
  </div>
  {#if loading}
    <p> ...</p>
  {:else if error}
    <p>Error: {error.message}</p>
  {:else}
    {#each eventData as { eventname, numberteams, Championship1, Championship2, Championship3, Championship4, Runner_up1, Runner_up2, Runner_up3, Runner_up4, Place3rd1, Place3rd2, Place3rd3, Place3rd4, ok, teamSize, Eventhost }, index}
      <div class="table-contents-wrapper">
        <div
          class="table-contents"
          on:click={() => toggleDetails(index)}
          on:keydown={handleKeyDown}
        >
          <div class="table-contents_eventname table-contents_cell">
            {eventname}
          </div>
          <div class="table-contents_member table-contents_cell">
            {#if numberteams === 24}{:else}
              {String(numberteams) + "x" + String(teamSize)}{/if}
          </div>
          <div class="table-contents_allwinner table-contents_cell">
            {Championship1}
          </div>
          {#if $nickname === "admin" || $nickname === "admin_m"}
            <div class="table-contents_ok table-contents_right">
              {#if ok === ""}
                <button on:click={() => approveEvent(index)}></button
                ><button on:click={() => deleteEvent(index)}></button
                >{:else}
                <button on:click={() => deleteandresetEvent(index)}></button
                >{/if}
            </div>{:else}
            <div class="table-contents_ok table-contents_right">{ok}</div>{/if}
        </div>
        {#if showDetails[index]}
          <div class="table-contents_detail {showDetails[index] ? 'show' : ''}">
             <br />
             : {Eventhost}<br />
             : {Championship1 ? Championship1 : ""}
            {Championship2 ? Championship2 : ""}
            {Championship3 ? Championship3 : ""}
            {Championship4 ? Championship4 : ""}<br />
             : {Runner_up1 ? Runner_up1 : ""}
            {Runner_up2 ? Runner_up2 : ""}
            {Runner_up3 ? Runner_up3 : ""}
            {Runner_up4 ? Runner_up4 : ""}<br />
            3 : {Place3rd1 ? Place3rd1 : ""}
            {Place3rd2 ? Place3rd2 : ""}
            {Place3rd3 ? Place3rd3 : ""}
            {Place3rd4 ? Place3rd4 : ""}
          </div>
        {/if}
      </div>
    {/each}
  {/if}
</div>

{#if $nickname}
  <div class="fixed-button-div">
    <button on:click={() => clickformopen("newevent")}><br /></button>
  </div>
{/if}

<style>
  .table-head_eventname,
  .table-contents_eventname {
    width: 30%;
  }

  .table-head_member,
  .table-contents_member {
    width: 15%;
  }

  .table-head_allwinner,
  .table-contents_allwinner {
    width: 25%;
  }

  .table-head_ok,
  .table-contents_ok {
    width: 35%;
  }
  .table-contents:hover {
text-decoration: underline;

  }

</style>
