<script>
  import { nickname, mode, clickformopen, refresh_access_token } from "./store";
  import { onMount } from "svelte";
  import { get } from "svelte/store";

  let email = "Loading";
  let tscore = "Loading";
  let bscore = "Loading";
  let lscore = "Loading";
  let date = "Loading";
  let wins = "Loading";
  let loses = "Loading";

  //        ()
// GET   fetch 
async function fetchGameData() {
  let checkjwt = localStorage.getItem("d2rpvpjwtToken");

  if (!checkjwt) {
    alert(" ");
    return;
  }

  const fetchData = async () => {
    const endpoint = $mode ? "/user_data_m" : "/user_data";
    const response = await fetch(endpoint, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${checkjwt}`,
      },
    });

    return response;
  };

  try {
    let response = await fetchData();

    if (response.status === 200) {
      //  JSON  .
      const data = await response.json();
      email = data.email;
      tscore = data.tscore;
      lscore = data.lscore;
      bscore = data.bscore;
      date = data.lastdate;
      wins = data.countwin;
      loses = data.countlose;
    } else if (response.status === 403) {
      await refresh_access_token();
      checkjwt = localStorage.getItem("d2rpvpjwtToken");

      response = await fetchData(); //     

      if (response.status === 200) {
        //  JSON  .
        const data = await response.json();
        email = data.email;
        tscore = data.tscore;
        lscore = data.lscore;
        bscore = data.bscore;
        date = data.lastdate;
        wins = data.countwin;
        loses = data.countlose;
      } else {
        alert(` : ${response.status}`);
      }
    } else {
      alert(` : ${response.status}`);
    }
  } catch (error) {
    console.error("    :", error);
    alert("    ");
  }
}

  onMount(() => {
    fetchGameData();
  });
</script>

<div class="outline">
  <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">
      {$mode ? $nickname.replace("_m", "") : $nickname}
    </div>
  </div>
  <div class="info-flexbox">
    <div class="info-name-data">{email}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name">
      <button on:click={() => clickformopen("changepw")}> </button>
    </div>
    <div class="info-data">
      <button on:click={() => clickformopen("changeemail")}> </button>
    </div>
  </div>
  <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">{tscore}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">{bscore}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">{lscore}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">{date}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">{wins}/{loses}</div>
  </div>
  <div class="info-data">
    <button on:click={() => clickformopen("deleteaccount")}> </button>
  </div>
</div>

<style>
  .outline {
    margin-top: 20px;
  }
  .info-flexbox {
    display: flex;
  }
  .info-name {
    width: 300px;
    text-align: center;
    font-size: 20px;
  }
  .info-data {
    width: 300px;
    text-align: center;
    font-size: 20px;
  }
  .info-name-data {
    text-align: center;
    width: 600px;
    font-size: 20px;
  }
</style>
