<script>
  import { onMount } from "svelte";
  import { writable } from "svelte/store";

  onMount(async () => {
  });

  //  
  let mycharlv = writable(99);
  let myhp = writable(0);
  let mymindmg = writable(0);
  let mymaxdmg = writable(0);
  let myar = writable(0);
  let mydf = writable(0);
  let myframe = writable(4);
  let mydfoff = writable(0);
  let myreduce = writable(0);
  let mycrush = writable(0);
  let mycs = writable(0);
  let myds = writable(0);
  let mydodge = writable(0);
  let myopenwound = writable(0);
  let mythorns = writable(0);
  let myclass = writable(0);

  //  
  let yourcharlv = writable(99);
  let yourhp = writable(0);
  let yourmindmg = writable(0);
  let yourmaxdmg = writable(0);
  let yourar = writable(0);
  let yourdf = writable(0);
  let yourframe = writable(4);
  let yourdfoff = writable(0);
  let yourreduce = writable(50);
  let yourcrush = writable(0);
  let yourcs = writable(0);
  let yourds = writable(0);
  let yourdodge = writable(0);
  let youropenwound = writable(0);
  let yourthorns = writable(0);
  let yourclass = writable(0);

  //  
  let iterations = writable(2000);
  let win = writable(0);
  let lose = writable(0);
  let draw = writable(0);
  let winRate = writable(0);

  //   
  const SaveMyStat = () => {
    const myformData = {
      charlv: $mycharlv,
      hp: $myhp,
      maxdmg: $mymaxdmg,
      mindmg: $mymindmg,
      ar: $myar,
      df: $mydf,
      frame: $myframe,
      dfoff: $mydfoff,
      reduce: $myreduce,
      crush: $mycrush,
      cs: $mycs,
      ds: $myds,
      dodge: $mydodge,
      openwound: $myopenwound,
      thorns: $mythorns,
      class: $myclass,
    };
    const jsonStr = JSON.stringify(myformData);
    const blob = new Blob([jsonStr], { type: "application/json" });
    const url = URL.createObjectURL(blob);
    const link = document.createElement("a");
    link.download = ".json";
    link.href = url;
    link.click();
  };

  const SaveYourStat = () => {
    const yourformData = {
      charlv: $yourcharlv,
      hp: $yourhp,
      maxdmg: $yourmaxdmg,
      mindmg: $yourmindmg,
      ar: $yourar,
      df: $yourdf,
      frame: $yourframe,
      dfoff: $yourdfoff,
      reduce: $yourreduce,
      crush: $yourcrush,
      cs: $yourcs,
      ds: $yourds,
      dodge: $yourdodge,
      openwound: $youropenwound,
      thorns: $yourthorns,
      class: $yourclass,
    };
    const jsonStr = JSON.stringify(yourformData);
    const blob = new Blob([jsonStr], { type: "application/json" });
    const url = URL.createObjectURL(blob);
    const link = document.createElement("a");
    link.download = ".json";
    link.href = url;
    link.click();
  };

  const handleMyStatFile = (event) => {
    const file = event.target.files[0];
    if (!file) {
      return;
    }

    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        const parsedJson = JSON.parse(event.target.result);
        if (
          parsedJson.charlv == undefined ||
          parsedJson.hp == undefined ||
          parsedJson.maxdmg == undefined ||
          parsedJson.mindmg == undefined ||
          parsedJson.ar == undefined ||
          parsedJson.df == undefined ||
          parsedJson.frame == undefined ||
          parsedJson.dfoff == undefined ||
          parsedJson.reduce == undefined ||
          parsedJson.crush == undefined ||
          parsedJson.cs == undefined ||
          parsedJson.ds == undefined ||
          parsedJson.dodge == undefined ||
          parsedJson.openwound == undefined ||
          parsedJson.thorns == undefined ||
          parsedJson.class == undefined
        ) {
          throw Error;
        }

        mycharlv.set(parsedJson.charlv);
        myhp.set(parsedJson.hp);
        mymaxdmg.set(parsedJson.maxdmg);
        mymindmg.set(parsedJson.mindmg);
        myar.set(parsedJson.ar);
        mydf.set(parsedJson.df);
        mydfoff.set(parsedJson.dfoff);
        myreduce.set(parsedJson.reduce);
        mycrush.set(parsedJson.crush);
        myds.set(parsedJson.ds);
        myframe.set(parsedJson.frame);
        mycs.set(parsedJson.cs);
        mydodge.set(parsedJson.dodge);
        myopenwound.set(parsedJson.openwound);
        mythorns.set(parsedJson.thorns);
        myclass.set(parsedJson.class);
      } catch (error) {
        alert(" Json  ");
      }
    };
    reader.readAsText(file);
  };

  const handleYourStatFile = (event) => {
    const file = event.target.files[0];
    if (!file) {
      return;
    }

    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        const parsedJson = JSON.parse(event.target.result);
        if (
          parsedJson.charlv == undefined ||
          parsedJson.hp == undefined ||
          parsedJson.maxdmg == undefined ||
          parsedJson.mindmg == undefined ||
          parsedJson.ar == undefined ||
          parsedJson.df == undefined ||
          parsedJson.frame == undefined ||
          parsedJson.dfoff == undefined ||
          parsedJson.reduce == undefined ||
          parsedJson.crush == undefined ||
          parsedJson.cs == undefined ||
          parsedJson.ds == undefined ||
          parsedJson.dodge == undefined ||
          parsedJson.openwound == undefined ||
          parsedJson.thorns == undefined ||
          parsedJson.class == undefined
        ) {
          throw Error;
        }

        yourcharlv.set(parsedJson.charlv);
        yourhp.set(parsedJson.hp);
        yourmaxdmg.set(parsedJson.maxdmg);
        yourmindmg.set(parsedJson.mindmg);
        yourar.set(parsedJson.ar);
        yourdf.set(parsedJson.df);
        yourdfoff.set(parsedJson.dfoff);
        yourreduce.set(parsedJson.reduce);
        yourcrush.set(parsedJson.crush);
        yourds.set(parsedJson.ds);
        yourframe.set(parsedJson.frame);
        yourcs.set(parsedJson.cs);
        yourdodge.set(parsedJson.dodge);
        youropenwound.set(parsedJson.openwound);
        yourthorns.set(parsedJson.thorns);
        yourclass.set(parsedJson.class);
      } catch (error) {
        alert(" Json  ");
      }
    };
    reader.readAsText(file);
  };

  const handleCalculate = async () => {

    win.set("-");
    lose.set("-");
    draw.set("-");
    winRate.set("-");
    const requestData = {
      myhp: $myhp,
      yourhp: $yourhp,
      myframe: $myframe,
      yourframe: $yourframe,
      myclass: $myclass,
      yourclass: $yourclass,
      mycharlv: $mycharlv,
      yourcharlv: $yourcharlv,
      iterations: $iterations,
      myar: $myar,
      yourar: $yourar,
      mydf: $mydf,
      yourdf: $yourdf,
      mydfoff: $mydfoff,
      yourdfoff: $yourdfoff,
      mydodge: $mydodge,
      yourdodge: $yourdodge,
      mycrush: $mycrush,
      yourcrush: $yourcrush,
      myopenwound: $myopenwound,
      youropenwound: $youropenwound,
      mymaxdmg: $mymaxdmg,
      mymindmg: $mymindmg,
      yourmaxdmg: $yourmaxdmg,
      yourmindmg: $yourmindmg,
      myreduce: $myreduce,
      yourreduce: $yourreduce,
      mycs: $mycs,
      myds: $myds,
      yourcs: $yourcs,
      yourds: $yourds,
      mythorns: $mythorns,
      yourthorns: $yourthorns,
    };

    try {
      const response = await fetch("/calculate", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(requestData),
      });

      if (response.ok) {
        const data = await response.json();
        win.set(data.winCount);
        lose.set(data.loseCount);
        draw.set(data.drawCount);
        const roundedWinRate = Number(data.winRate.toFixed(2));
        winRate.set(roundedWinRate);
      } else {
        console.error("Error calculating:", response.statusText);
      }
    } catch (error) {
      console.error("Error calculating:", error);
      alert("  ", error);
    } finally {
    }
  };
</script>

<div class="main_data table-outline">
  <div class="table-head">  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$mycharlv} class="numberwidth" /> 
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$mymindmg} class="numberwidth" />  
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$mymaxdmg} class="numberwidth" />  
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$myar} class="numberwidth" />  
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$mydfoff} class="numberwidth" /> 
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$mydf} class="numberwidth" />  
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$myhp} class="numberwidth" />  
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$myframe} class="numberwidth" />  
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$myreduce} class="numberwidth" />  
        %
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$mycrush} class="numberwidth" /> 
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$myds} class="numberwidth" /> 
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$mycs} class="numberwidth" /> 
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$myopenwound} class="numberwidth" /> 
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$mythorns} class="numberwidth" /> 
      </div>
    </div>
  </div>
  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$mydodge} class="numberwidth" /> 
        ( )
      </div>
      <div class="table-divide">
        <select bind:value={$myclass} class="namewidth">
          <option value="1"></option>
          <option value="2"></option>
          <option value="3"></option>
          <option value="4"></option>
        </select> 
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <button on:click={SaveMyStat}>   </button>
      </div>
      <br />
      <div class="table-divide">
        <input
          type="file"
          id="myfileUpload"
          style="display: none"
          on:change={handleMyStatFile}
          accept="application/json"
        />
        <label for="myfileUpload" class="button"
          >   </label
        >
      </div>
    </div>
  </div>

  <div class="table-head">  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$yourcharlv} class="numberwidth" /> 
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$yourmindmg} class="numberwidth" /> 
        
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$yourmaxdmg} class="numberwidth" /> 
        
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$yourar} class="numberwidth" />  
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$yourdfoff} class="numberwidth" /> 
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$yourdf} class="numberwidth" />  
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$yourhp} class="numberwidth" />  
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$yourframe} class="numberwidth" /> 
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$yourreduce} class="numberwidth" /> 
         %
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$yourcrush} class="numberwidth" /> 
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$yourds} class="numberwidth" /> 
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$yourcs} class="numberwidth" /> 
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$youropenwound} class="numberwidth" /> 
      </div>

      <div class="table-divide">
        <input type="number" bind:value={$yourthorns} class="numberwidth" /> 
      </div>
    </div>
  </div>
  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <input type="number" bind:value={$yourdodge} class="numberwidth" /> 
        ( )
      </div>
      <div class="table-divide">
        <select bind:value={$yourclass} class="namewidth">
          <option value="1"></option>
          <option value="2"></option>
          <option value="3"></option>
          <option value="4"></option>
        </select>
        
      </div>
    </div>
  </div>
  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <button on:click={SaveYourStat}>   </button>
      </div>
      <br />
      <div class="table-divide">
        <input
          type="file"
          id="yourfileUpload"
          style="display: none"
          on:change={handleYourStatFile}
          accept="application/json"
        />
        <label class="button" for="yourfileUpload"
          >   </label
        >
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="table-divide">
        <label
          >  ?
          <input type="number" bind:value={$iterations} class="numberwidth" />
        </label>
      </div>

      <div class="table-divide">
        <button on:click={handleCalculate}> </button>
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="left">
         {#if $win === "-"}{:else}{/if}<br />
         : {$win} <br />
         : {$lose} <br />
         : {$draw} <br />
         : {$winRate} %
      </div>
    </div>
  </div>

  <div class="table-contents-wrapper">
    <div class="table-contents">
      <div class="left">
         
        <br />
                  .
            .(   X)  
                 
             .     
        ( X)     
        .         
        .     2    .
      </div>
    </div>
  </div>
</div>

