<script>
    import {
      mode,
      clickformopen, refresh_access_token,
      nickname

    } from "./store.js";
  
    let pw = "";
  
  
    async function delete_account() {
    const pwdata = {
      nowpw: pw,
    };
  
    let checkjwt = localStorage.getItem("d2rpvpjwtToken");
  
    if (!checkjwt) {
      alert(" ");
      return;
    }
  
    const deleteAccountRequest = async () => {
      const endpoint = $mode
        ? "/delete_account_m"
        : "/delete_account";
      const response = await fetch(endpoint, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${checkjwt}`,
        },
        body: JSON.stringify(pwdata),
      });
  
      return response;
    };
  
    try {
      let response = await deleteAccountRequest();
  
      if (response.status === 200) {
        alert("  ");
        clickformopen(null);
        localStorage.removeItem("d2rpvpjwtToken")
localStorage.removeItem("d2rpvprefreshToken")
nickname.set(null)

      } else if (response.status === 403) {
        await refresh_access_token();
        checkjwt = localStorage.getItem("d2rpvpjwtToken");
  
        response = await deleteAccountRequest(); //     
  
        if (response.status === 200) {
            alert("  ");
            clickformopen(null);
            localStorage.removeItem("d2rpvpjwtToken")
localStorage.removeItem("d2rpvprefreshToken")
nickname.set(null)

        } else {
          alert(` : ${response.status}`);
        }
      } else {
        alert(` : ${response.status}`);
      }
    } catch (error) {
      console.error(" :", error);
      alert(" : " + error.message);
    }
  }
  
  </script>
  
  <div class="left">
      
    <input class="namewidth" type="password" bind:value={pw} />
    <button on:click={() => delete_account()}> </button>
  </div>
  