<script>
  import { onMount } from "svelte";
  import { key } from "../store.js";
  let rankData = [];
  let filteredData = [];
  let loading = true;
  let error = null;
  let showDetails = [];
  let selectfilter = "0";

  async function fetchData() {
    try {
      const response = await fetch("/rankdata_m");
      if (!response.ok) {
        throw new Error(" ");
      }
      rankData = await response.json();
      showDetails = new Array(rankData.length).fill(false); // showDetails  
      filterData(); //     
    } catch (err) {
      error = err;
    } finally {
      loading = false;
    }
  }

  function filterData() {
    if (selectfilter === 0) {
      filteredData = rankData;
    } else if (selectfilter === 1) {
      filteredData = rankData.filter((row) => row[6] + row[7] >= 1);
    } else {
      filteredData = rankData.filter((row) => row[6] + row[7] >= selectfilter);
    }
    showDetails = new Array(filteredData.length).fill(false); //    showDetails  
  }

  //     
  onMount(() => {
    const unsubscribe = key.subscribe((value) => {
      fetchData();
    });

    //     
    return () => {
      unsubscribe();
    };
  });

  function toggleDetails(index) {
    showDetails[index] = !showDetails[index];
    showDetails = [...showDetails]; //   Svelte  
  }

  function handleKeyDown(event, index) {
    if (event.key === "Enter" || event.key === " ") {
      toggleDetails(index);
    }
  }
</script>

<!-- <div class="main_data">
  <select
    class="namewidth center"
    bind:value={selectfilter}
    on:change={() => filterData()}
  >
    <option value="0">  </option>
    <option value="1">   </option>
    <option value="10"> 10   </option>
  </select>
</div> -->
<div class="table-outline main_data rank">
  <div class="table-head">
    <div class="table-head_rank table-contents_cell"></div>
    <div class="table-head_nickname table-contents_cell"></div>
    <div class="table-head_winrate table-contents_cell"></div>
    <div class="table-head_score table-contents_right-cell"></div>
  </div>
  {#if loading}
    <p> ...</p>
  {:else if error}
    <p>Error: {error.message}</p>
  {:else}
    {#each filteredData as row, index}
      <div class="table-contents-wrapper">
        <div
          class="table-contents"
          on:click={() => toggleDetails(index)}
          on:keydown={handleKeyDown}
        >
          <div class="table-contents_rank table-contents_cell">
            {#if row[0] / rankData.length <= 0.1}<img
                class="ranksize"
                src="/img/diamond.png"
                alt="10%"
              />
            {:else if row[0] / rankData.length <= 0.25}<img
                class="ranksize"
                src="/img/platinum.png"
                alt="25%"
              />
            {:else if row[0] / rankData.length <= 0.45}<img
                class="ranksize"
                src="/img/gold.png"
                alt="45%"
              />
            {:else if row[0] / rankData.length <= 0.7}<img
                class="ranksize"
                src="/img/silver.png"
                alt="70%"
              />
            {:else}<img class="ranksize" src="/img/bronze.png" alt="100%" />
            {/if}
          </div>
          <!--  () -->
          <div class="table-contents_nickname table-contents_cell">
            {row[1].replace("_m", "")}
          </div>
          <!--  -->
          <div class="table-contents_winrate table-contents_cell">
            {row[6] + row[7] == 0
              ? 0
              : Math.round((row[6] / (row[6] + row[7])) * 10000) / 100}
          </div>
          <!--  ( ) -->
          <div class="table-contents_score table-contents_right-cell">
            {row[3]}
          </div>
          <!--  (BScore + LScore) -->
        </div>
        {#if showDetails[index]}
          <div class="table-contents_detail show">
            : {row[4]} <br />
            : {row[5]} <br />
            : {row[6] + row[7]} <br />
            : {row[6]} <br />
            : {row[7]} <br />
            : {row[8]}
          </div>
        {/if}
      </div>
    {/each}
  {/if}
</div>

<style>
  .table-head_rank,
  .table-contents_rank {
    width: 15%;
  }

  .table-head_nickname,
  .table-contents_nickname {
    width: 40%;
  }

  .table-head_winrate,
  .table-contents_winrate {
    width: 25%;
  }

  .table-head_score,
  .table-contents_score {
    width: 20%;
  }
  .table-contents:hover {
text-decoration: underline;

  }

</style>
