<script>
  import {
    mode,
    clickformopen, refresh_access_token
  } from "./store.js";

  let pw = "";
  let newemail = "";


  async function changeemail() {
  const emaildata = {
    nowpw: pw,
    newemail: newemail,
  };

  let checkjwt = localStorage.getItem("d2rpvpjwtToken");

  if (!checkjwt) {
    alert(" ");
    return;
  }

  const sendEmailChangeRequest = async () => {
    const endpoint = $mode
      ? "/process_changeemail_m"
      : "/process_changeemail";
    const response = await fetch(endpoint, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${checkjwt}`,
      },
      body: JSON.stringify(emaildata),
    });

    return response;
  };

  try {
    let response = await sendEmailChangeRequest();

    if (response.status === 200) {
      alert("  ");
      clickformopen(null);
    } else if (response.status === 403) {
      await refresh_access_token();
      checkjwt = localStorage.getItem("d2rpvpjwtToken");

      response = await sendEmailChangeRequest(); //     

      if (response.status === 200) {
        alert("  ");
        clickformopen(null);
      } else {
        alert(` : ${response.status}`);
      }
    } else {
      alert(` : ${response.status}`);
    }
  } catch (error) {
    console.error(" :", error);
    alert(" : " + error.message);
  }
}

</script>

<div class="left">
  
  <input class="namewidth" type="password" bind:value={pw} />
   
  <input class="namewidth" type="text" bind:value={newemail} />
  <button on:click={() => changeemail()}> </button>
</div>
