<script>
  let recordData = [];
  let loading = true;
import { refresh_access_token } from "./store.js";
  import { onMount } from "svelte";

  async function fetchGameData() {
    let checkjwt = localStorage.getItem("d2rpvpjwtToken")
    if (checkjwt) {
      try {
        // fetch GET  .
        const response = await fetch("/no_approved_record", {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${checkjwt}`,
          },
        });

        //  JSON  .
        recordData = await response.json();
        loading = false;
      } catch (error) {
        alert("    ");
      }
    } else {
      alert(" ");
    }
  }

  onMount(async () => {
    fetchGameData();

  });
  async function approveRecord(orderNum) {
  const data = { orderNum: orderNum };
  let checkjwt = localStorage.getItem("d2rpvpjwtToken");

  if (!checkjwt) {
    alert(" ");
    return;
  }

  const sendApproveRequest = async () => {
    const response = await fetch("/approve-record", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${checkjwt}`,
      },
      body: JSON.stringify(data),
    });

    return response;
  };

  try {
    let response = await sendApproveRequest();

    if (response.ok) {
      alert(" ");
      fetchGameData();
    } else if (response.status === 403) {
      await refresh_access_token();
      checkjwt = localStorage.getItem("d2rpvpjwtToken");

      response = await sendApproveRequest(); //     

      if (response.ok) {
        alert(" ");
        fetchGameData();
      } else {
        alert(` : ${response.status}`);
      }
    } else {
      alert(` : ${response.status}`);
    }
  } catch (error) {
    console.error(" :", error);
    alert(" : " + error.message);
  }
}

  async function deleteRecord(orderNum) {
  const data = { orderNum: orderNum };
  let checkjwt = localStorage.getItem("d2rpvpjwtToken");

  if (!checkjwt) {
    alert(" ");
    return;
  }

  const sendDeleteRequest = async () => {
    const response = await fetch("/delete-record", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${checkjwt}`,
      },
      body: JSON.stringify(data),
    });

    return response;
  };

  try {
    let response = await sendDeleteRequest();

    if (response.status === 200) {
      alert(" ");
      fetchGameData();
    } else if (response.status === 403) {
      await refresh_access_token();
      checkjwt = localStorage.getItem("d2rpvpjwtToken");
      
      response = await sendDeleteRequest(); //     

      if (response.status === 200) {
        alert(" ");
        fetchGameData();
      } else {
        alert(` : ${response.status}`);
      }
    } else {
      alert(` : ${response.status}`);
    }
  } catch (error) {
    console.error(" :", error);
    alert(" : " + error.message);
  }
}

</script>

<table>
  <tr>
    <th class="record-table-date">  </th>
    <th class="record-table-loser">  </th>
    <th class="record-table-score">  </th>
    <th class="record-table-ok">  </th>
  </tr>
  {#if loading}<tr> <td colspan="4" class="text-center"> ..</td></tr>{/if}
  {#if !loading && recordData.length == 0}<tr>
      <td colspan="4" class="text-center"> </td></tr
    >{/if}

  {#each recordData as row}
    <tr>
      <td
        >{new Date(row.Date).toLocaleString("en-US", {
          month: "2-digit",
          day: "2-digit",
        })}</td
      >
      <!--    JSON . -->
      <td>{row.Loser}</td>
      <td>{row.LScore}</td>
      <td>
        <button on:click={() => approveRecord(row.OrderNum)}></button>
        <button on:click={() => deleteRecord(row.OrderNum)}></button>
      </td>
    </tr>
  {/each}
</table>

<style>
  table {
    width: 90%;
    margin-top: 20px;
  }
  .record-table-date {
    width: 15%;
  }
  .record-table-loser {
    width: 40%;
  }
  .record-table-score {
    width: 15%;
  }
  .record-table-ok {
    width: 20%;
  }
</style>
